.container {
	width: 600px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}
.btn {
	position: relative;
	width: 155px;
	height: 50px;
}
.btn button {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background: rgba(255, 255, 255, 0.05);
	box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
	border-top: 1px solid rgba(255, 255, 255, 0.1);
	border-bottom: 1px solid rgba(255, 255, 255, 0.1);
	border-radius: 30px;
	color: #fff;
	z-index: 1;
	font-weight: 400;
	letter-spacing: 1px;
	text-transform:capitalize;
	overflow: hidden;
	transition: 0.5s;
	backdrop-filter: blur(15px);
}
.btn:hover button {
	letter-spacing: 3px;
}
.btn button::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 50%;
	height: 100%;
	background: linear-gradient(to left, rgba(255, 255, 255, 0.15), transparent);
	transform: skewX(45deg) translateX(0);
	transition: 0.5s;
}
.btn:hover button::before {
	transform: skewX(45deg) translateX(200%);
}
.btn::before {
	content: "";
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	bottom: -5px;
	width: 30px;
	height: 10px;
	background: #f00;
	border-radius: 10px;
	transition: 0.5s;
	transition-delay: 0s;
}
.btn:hover::before {
	bottom: 0;
	height: 50%;
	width: 80%;
	border-radius: 30px;
	transition-delay: 0.5s;
}
.btn::after {
	content: "";
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	top: -5px;
	width: 30px;
	height: 10px;
	background: #f00;
	border-radius: 10px;
	transition: 0.5s;
	transition-delay: 0s;
}
.btn:hover::after {
	top: 0;
	height: 50%;
	width: 80%;
	border-radius: 30px;
	transition-delay: 0.5s;
}
.btn:nth-child(3):after,
.btn:nth-child(3)::before {
	background: #ff1f1f;
	box-shadow: 0 0 5px #ff1f1f, 0 0 15px #ff1f1f, 0 0 30px #ff1f1f, 0 0 60px #ff1f1f;
}
.btn:nth-child(2):after,
.btn:nth-child(2)::before {
	background: #fae105;
	box-shadow: 0 0 5px #fae105, 0 0 15px #fae105, 0 0 30px #fae105, 0 0 60px #fae105;
}
.btn:nth-child(1):after,
.btn:nth-child(1)::before {
	background: #01a51d;
	box-shadow: 0 0 5px #01a51d, 0 0 15px #01a51d, 0 0 30px #01a51d, 0 0 60px #01a51d;
}
